.Youtube {
  height: 110vh;
  background-color: rgba(0, 0, 0, 0.918);
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(7, 1fr);
  margin: 0 -4rem;
  position: fixed;
  top: -1rem;
  z-index: 150000;
  width: 100vw;
}

@media only screen and (max-width: 1200px) {
  .Youtube {
    height: 103vh;
  }
}

@font-face {
  font-family: Para;
  src: url(/randomApp/static/media/Codedemo1.eaa1b4b3.otf) format("opentype");
}

